// region create gallery content
const galleryContainer = document.querySelector('.gallery');
const galleryContent = document.createDocumentFragment();

images.forEach(image => {
    const largeImage = image.original;
    const smallImage = image.preview;
    // create li
    const liElement = document.createElement('li');
    liElement.classList.add('gallery-item');

    // create a
    const linkElement = document.createElement('a');
    linkElement.classList.add('gallery-link');
    linkElement.href = largeImage;

    // create img
    const imgElement = document.createElement('img');
    imgElement.classList.add('gallery-image');
    imgElement.src = smallImage;
    imgElement.alt = image.description;

    // append elements
    linkElement.append(imgElement);
    liElement.append(linkElement);
    galleryContent.append(liElement);
});

galleryContainer.append(galleryContent);
// endregion

const gallery = new SimpleLightbox('.gallery a', {
    captionPosition: 'bottom',
    captionSelector: 'img',
    captionDelay: 250,
    captionsData: 'alt',
});
